  margin: 0 0 1.5em 0;
  overflow: auto;
}
.wp_syntax table {
  border-collapse: collapse;
}
.wp_syntax div,
.wp_syntax td {
  vertical-align: top;
  padding: 2px 4px;
}
.wp_syntax pre {
  margin: 0;
  width: auto;
  float: none;
  clear: none;
  overflow: visible;
}
.code {
